import React, { Component } from 'react';

export default (importComponent) => (
  class extends Component {
    state = {
      component: null
    }

    componentDidMount() {
      importComponent()
        .then(cmp => this.setState({
          component: cmp.default
        }));
    }

    render() {
      const C = this.state.component;
      return C ? <C {...this.props} /> : null;
    }
  }
);



